<template>
  <div class="whitepay-page">
      <a href="javascript:;" class="whitepay-btn" @click="doApplyPay"></a>
  </div>
</template>

<script>
import URL from '@/http/url'
import util from '@/utils/utils'
import { mapGetters } from 'vuex'

export default {
  name: 'sgwhitepay',
  data() {
    return {
      message: ''
    }
  },
  computed: {
    ...mapGetters([
      'ucId',
      'accessToken',
    ])
  },
  methods: {
    doApplyPay() {
      this.$http.get(URL.GET_PROCOTOL_BAITIAO + '?id=1199').then(res => {
        // console.log(res);
        this.message = res.data.data
        this.$dialog.confirm({
          title: '智家商城白条使用须知',
          message: this.message,
          confirmButtonText: '同意',
          cancelButtonText: '不同意'
        }).then(() => {
          this.doAgree()
        }).catch(() => {
          // on cancel
        });
      })
    },
    doAgree() {
      let url = `${URL.WHITE_SHOWS_APPLY}?backUrl=${encodeURIComponent(window.location.href)}&userId=${this.ucId}&token=${this.accessToken}`
      this.$http.get(url).then(res => {
        let result = res.data
        if (result.success) {
          // window.location.href = result.redirectUrl
          util.openNewWindow(result.data.redirectUrl, true)
        } else {
          if (result.errorCode === '-100' || result.message.includes('未登录')) {
            this.$store.dispatch('doLogin');
          } else if (result.message) {
            this.$toast(result.message);
          }
        }
      })
    },
  }
}
</script>

